import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import './CreateToken.css';

//  ABI
const TOKEN_ABI = [
    {
        "inputs": [
            {
                "internalType": "string",
                "name": "name",
                "type": "string"
            },
            {
                "internalType": "string",
                "name": "symbol",
                "type": "string"
            },
            {
                "internalType": "uint256",
                "name": "collateralAmount",
                "type": "uint256"
            },
            {
                "internalType": "uint256",
                "name": "totalSupply",
                "type": "uint256"
            }
        ],
        "name": "createToken",
        "outputs": [],
        "stateMutability": "payable",
        "type": "function"
    }
];

// 
const CONTRACT_ADDRESS = "0x3B236CEeB75207cD99068F21B871F7cE91D1f113";

// 
const formatNumber = (number) => {
    const parts = number.toString().split('.');
    if (parts.length > 1) {
        return parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",") + '.' + parts[1];
    }
    return parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
};

// 
const removeCommas = (str) => {
    return str.replace(/,/g, '');
};

const CreateToken = ({ account, onTokenCreated }) => {
    const [tokenName, setTokenName] = useState('');
    const [tokenSymbol, setTokenSymbol] = useState('');
    const [totalSupply, setTotalSupply] = useState('');
    const [ethfAmount, setEthfAmount] = useState('');
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(false);
    const [balance, setBalance] = useState('0');
    const [exchangeRatio, setExchangeRatio] = useState('');
    const [success, setSuccess] = useState('');

    //  ETHF 
    const getBalance = async () => {
        try {
            if (!window.ethereum || !account) return;

            const provider = new ethers.BrowserProvider(window.ethereum);
            const balance = await provider.getBalance(account);
            setBalance(ethers.formatEther(balance));
        } catch (err) {
            console.error(':', err);
        }
    };

    // 
    useEffect(() => {
        getBalance();
    }, [account]);

    // 
    const checkNetwork = async () => {
        if (!window.ethereum) {
            throw new Error(' MetaMask!');
        }

        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        // ETHF  ID  513100
        if (chainId !== '0x7d44c') {
            throw new Error(' ETHF !');
        }
    };

    const handleCreateToken = async () => {
        try {
            setError('');
            setLoading(true);

            // 
            await checkNetwork();

            // 
            if (!tokenName || !tokenSymbol || !totalSupply || !ethfAmount) {
                throw new Error('');
            }

            // 
            const totalSupplyNum = Number(removeCommas(totalSupply));
            const ethfAmountNum = Number(removeCommas(ethfAmount));
            const balanceNum = Number(balance);

            if (isNaN(totalSupplyNum) || totalSupplyNum <= 0) {
                throw new Error('0');
            }

            if (isNaN(ethfAmountNum) || ethfAmountNum <= 0) {
                throw new Error(' ETHF 0');
            }

            // 
            if (ethfAmountNum > balanceNum) {
                throw new Error(`ETHF : ${formatNumber(balance)} ETHF`);
            }

            //  Wei
            const ethfAmountWei = ethers.parseEther(removeCommas(ethfAmount));
            //  Wei
            const totalSupplyWei = ethers.parseUnits(removeCommas(totalSupply), 18);

            // 
            const provider = new ethers.BrowserProvider(window.ethereum);
            const signer = await provider.getSigner();
            const contract = new ethers.Contract(CONTRACT_ADDRESS, TOKEN_ABI, signer);

            // 
            const tx = await contract.createToken(
                tokenName,
                tokenSymbol,
                ethfAmountWei,
                totalSupplyWei,
                { value: ethfAmountWei }
            );

            await tx.wait();
            alert('');
            setTokenName('');
            setTokenSymbol('');
            setTotalSupply('');
            setEthfAmount('');
            // 
            await getBalance();
            // 
            if (onTokenCreated) {
                onTokenCreated();
            }
            setSuccess('');
        } catch (err) {
            setError(err.message);
        } finally {
            setLoading(false);
        }
    };

    // 
    const handleNumberInput = (value, setter) => {
        // 
        const cleanValue = value.replace(/[^\d.]/g, '');
        // 
        const parts = cleanValue.split('.');
        if (parts.length > 2) {
            return;
        }
        // 
        const formattedValue = parts[0] ? formatNumber(parts[0]) : '';
        // 
        const finalValue = parts[1] ? `${formattedValue}.${parts[1]}` : formattedValue;
        setter(finalValue);
    };

    // 
    if (!account) {
        return (
            <div className="wallet-not-connected">
                <h2></h2>
                <p></p>
            </div>
        );
    }

    return (
        <div className="create-token-container">
            <div className="create-token-form">
                <h2></h2>
                <div className="form-group">
                    <label></label>
                    <input
                        type="text"
                        value={tokenName}
                        onChange={(e) => setTokenName(e.target.value)}
                        placeholder=""
                    />
                </div>
                <div className="form-group">
                    <label></label>
                    <input
                        type="text"
                        value={tokenSymbol}
                        onChange={(e) => setTokenSymbol(e.target.value)}
                        placeholder=""
                    />
                </div>
                <div className="form-group">
                    <label> ETHF </label>
                    <input
                        type="number"
                        value={ethfAmount}
                        onChange={(e) => setEthfAmount(e.target.value)}
                        placeholder=" ETHF "
                        step="0.000000000000000001"
                    />
                </div>
                <div className="form-group">
                    <label></label>
                    <input
                        type="number"
                        value={totalSupply}
                        onChange={(e) => setTotalSupply(e.target.value)}
                        placeholder=""
                        step="1"
                    />
                </div>
                {exchangeRatio && (
                    <div className="exchange-ratio">
                        : 1 ETHF = {exchangeRatio} {tokenSymbol}
                    </div>
                )}
                {error && <div className="error-message">{error}</div>}
                {success && <div className="success-message">{success}</div>}
                <button
                    className="create-button"
                    onClick={handleCreateToken}
                    disabled={!account || loading}
                >
                    {loading ? '...' : ''}
                </button>
            </div>
        </div>
    );
};

export default CreateToken; 