import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import './TokenList.css';

// 
const CONTRACT_ADDRESS = "0x3B236CEeB75207cD99068F21B871F7cE91D1f113";

//  ABI
const TOKEN_ABI = [
    {
        "inputs": [],
        "name": "getTokenList",
        "outputs": [
            {
                "internalType": "address[]",
                "name": "",
                "type": "address[]"
            }
        ],
        "stateMutability": "view",
        "type": "function"
    },
    {
        "inputs": [
            {
                "internalType": "string",
                "name": "symbol",
                "type": "string"
            }
        ],
        "name": "getTokenInfo",
        "outputs": [
            {
                "components": [
                    {
                        "internalType": "address",
                        "name": "tokenAddress",
                        "type": "address"
                    },
                    {
                        "internalType": "address",
                        "name": "creator",
                        "type": "address"
                    },
                    {
                        "internalType": "uint256",
                        "name": "collateralAmount",
                        "type": "uint256"
                    },
                    {
                        "internalType": "uint256",
                        "name": "totalSupply",
                        "type": "uint256"
                    },
                    {
                        "internalType": "uint256",
                        "name": "exchangeRate",
                        "type": "uint256"
                    }
                ],
                "internalType": "struct CollateralTokenFactory.TokenInfo",
                "name": "",
                "type": "tuple"
            }
        ],
        "stateMutability": "view",
        "type": "function"
    },
    {
        "inputs": [
            {
                "internalType": "address",
                "name": "",
                "type": "address"
            }
        ],
        "name": "tokenSymbols",
        "outputs": [
            {
                "internalType": "string",
                "name": "",
                "type": "string"
            }
        ],
        "stateMutability": "view",
        "type": "function"
    },
    {
        "inputs": [
            {
                "internalType": "address",
                "name": "tokenAddress",
                "type": "address"
            },
            {
                "internalType": "uint256",
                "name": "tokenAmount",
                "type": "uint256"
            }
        ],
        "name": "redeemCollateral",
        "outputs": [],
        "stateMutability": "nonpayable",
        "type": "function"
    }
];

// ERC20  ABI
const ERC20_ABI = [
    {
        "constant": true,
        "inputs": [{"name": "_owner", "type": "address"}],
        "name": "balanceOf",
        "outputs": [{"name": "balance", "type": "uint256"}],
        "type": "function"
    }
];

const TokenList = ({ account, refreshTrigger }) => {
    const [tokens, setTokens] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');
    const [redeeming, setRedeeming] = useState(false);
    const [redeemModal, setRedeemModal] = useState(false);
    const [selectedToken, setSelectedToken] = useState(null);
    const [redeemAmount, setRedeemAmount] = useState('');
    const [redeemEthfAmount, setRedeemEthfAmount] = useState('0');

    // 
    const getTokenList = async () => {
        try {
            setLoading(true);
            setError('');

            if (!window.ethereum) {
                throw new Error(' MetaMask!');
            }

            const provider = new ethers.BrowserProvider(window.ethereum);
            const contract = new ethers.Contract(CONTRACT_ADDRESS, TOKEN_ABI, provider);

            // 
            const tokenList = await contract.getTokenList();
            
            // 
            const tokenDetails = await Promise.all(
                tokenList.map(async (tokenAddress) => {
                    // 
                    const symbol = await contract.tokenSymbols(tokenAddress);
                    // 
                    const tokenInfo = await contract.getTokenInfo(symbol);
                    // 
                    const tokenContract = new ethers.Contract(tokenAddress, ERC20_ABI, provider);
                    const balance = await tokenContract.balanceOf(account);
                    return {
                        address: tokenAddress,
                        symbol: symbol,
                        creator: tokenInfo.creator,
                        collateralAmount: tokenInfo.collateralAmount,
                        totalSupply: tokenInfo.totalSupply,
                        exchangeRate: tokenInfo.exchangeRate,
                        userBalance: balance
                    };
                })
            );

            setTokens(tokenDetails);
        } catch (err) {
            setError(err.message);
        } finally {
            setLoading(false);
        }
    };

    //  ETHF 
    const calculateRedeemEthfAmount = (tokenAddress, amount) => {
        const token = tokens.find(t => t.address === tokenAddress);
        if (!token) return '0';
        
        const amountNum = Number(amount);
        if (isNaN(amountNum) || amountNum <= 0) return '0';
        
        //  ETHF 
        // redeemAmount = tokenAmount / exchangeRate
        //  Wei (18)
        const tokenAmountWei = ethers.parseUnits(amountNum.toString(), 18);
        const ethfAmount = (BigInt(tokenAmountWei) * BigInt(1e18)) / BigInt(token.exchangeRate);
        return ethers.formatEther(ethfAmount);
    };

    // 
    const openRedeemModal = (token) => {
        setSelectedToken(token);
        setRedeemAmount('');
        setRedeemEthfAmount('0');
        setRedeemModal(true);
    };

    // 
    const closeRedeemModal = () => {
        setRedeemModal(false);
        setSelectedToken(null);
        setRedeemAmount('');
        setRedeemEthfAmount('0');
    };

    // 
    const handleRedeemAmountChange = (e) => {
        const value = e.target.value;
        setRedeemAmount(value);
        if (selectedToken) {
            const ethfAmount = calculateRedeemEthfAmount(selectedToken.address, value);
            setRedeemEthfAmount(ethfAmount);
        }
    };

    // 
    const handleRedeem = async () => {
        try {
            setRedeeming(true);
            if (!window.ethereum) {
                throw new Error(' MetaMask!');
            }

            const amount = Number(redeemAmount);
            if (isNaN(amount) || amount <= 0) {
                throw new Error('');
            }

            if (amount > selectedToken.userBalance) {
                throw new Error('');
            }

            const provider = new ethers.BrowserProvider(window.ethereum);
            const signer = await provider.getSigner();
            const contract = new ethers.Contract(CONTRACT_ADDRESS, TOKEN_ABI, signer);

            //  Wei
            const tokenAmountWei = ethers.parseUnits(amount.toString(), 18);
            const tx = await contract.redeemCollateral(selectedToken.address, tokenAmountWei);
            await tx.wait();
            
            // 
            await getTokenList();
            closeRedeemModal();
        } catch (err) {
            // 
            if (err.code === 4001) {
                setError('');
            } else {
                setError(err.message);
            }
        } finally {
            setRedeeming(false);
        }
    };

    // 
    const copyToClipboard = (text) => {
        navigator.clipboard.writeText(text).then(() => {
            alert('');
        }).catch(err => {
            console.error(':', err);
        });
    };

    // 
    const formatAddress = (address) => {
        if (!address) return '';
        return `${address.slice(0, 6)}...${address.slice(-4)}`;
    };

    // 
    useEffect(() => {
        if (account) {
            getTokenList();
        }
    }, [account, refreshTrigger]);

    if (!account) {
        return (
            <div className="wallet-not-connected">
                <h2></h2>
                <p></p>
            </div>
        );
    }

    if (loading) {
        return <div className="loading">...</div>;
    }

    if (error) {
        return <div className="error">{error}</div>;
    }

    return (
        <div className="token-list">
            <h2></h2>
            {tokens.length === 0 ? (
                <p></p>
            ) : (
                <div className="tokens-grid">
                    {tokens.map((token, index) => (
                        <div key={index} className="token-card">
                            <div className="token-info">
                                <div className="token-address">
                                    <span>:</span>
                                    <div className="address-container">
                                        <code>{formatAddress(token.address)}</code>
                                        <button 
                                            className="copy-button"
                                            onClick={() => copyToClipboard(token.address)}
                                            disabled={!token.address}
                                        >
                                            
                                        </button>
                                    </div>
                                </div>
                                <div className="token-details">
                                    <p>: {formatAddress(token.creator)}</p>
                                    <p> ETHF: {ethers.formatEther(token.collateralAmount)}</p>
                                    <p>: {ethers.formatUnits(token.totalSupply, 18)}</p>
                                    <p>: 1 ETHF = {token.collateralAmount > 0 ? ethers.formatUnits((BigInt(token.totalSupply) * BigInt(1e18)) / BigInt(token.collateralAmount), 18) : '0'} {token.symbol}</p>
                                    <p>: {ethers.formatUnits(token.userBalance, 18)}</p>
                                </div>
                                {token.userBalance && token.userBalance > 0 && (
                                    <div className="token-actions">
                                        <button 
                                            className="redeem-button"
                                            onClick={() => openRedeemModal(token)}
                                            disabled={redeeming}
                                        >
                                            {redeeming ? '...' : ' ETHF'}
                                        </button>
                                    </div>
                                )}
                            </div>
                        </div>
                    ))}
                </div>
            )}

            {/*  */}
            {redeemModal && (
                <div className="modal-overlay">
                    <div className="modal-content">
                        <h3> {selectedToken?.symbol}</h3>
                        <div className="form-group">
                            <label></label>
                            <div className="input-with-max">
                                <input
                                    type="number"
                                    value={redeemAmount}
                                    onChange={handleRedeemAmountChange}
                                    placeholder=""
                                    min="0"
                                    max={ethers.formatUnits(selectedToken?.userBalance || 0, 18)}
                                    step="any"
                                />
                                <button 
                                    className="max-button"
                                    onClick={() => {
                                        if (selectedToken) {
                                            setRedeemAmount(ethers.formatUnits(selectedToken.userBalance, 18));
                                            setRedeemEthfAmount(calculateRedeemEthfAmount(selectedToken.address, ethers.formatUnits(selectedToken.userBalance, 18)));
                                        }
                                    }}
                                >
                                    MAX
                                </button>
                            </div>
                            <p className="balance-info">: {ethers.formatUnits(selectedToken?.userBalance || 0, 18)}</p>
                        </div>
                        <div className="redeem-info">
                            <p> ETHF : {redeemEthfAmount}</p>
                        </div>
                        <div className="modal-actions">
                            <button onClick={closeRedeemModal} className="cancel-button"></button>
                            <button 
                                onClick={handleRedeem} 
                                disabled={redeeming || !redeemAmount || Number(redeemAmount) <= 0 || Number(redeemAmount) > (selectedToken?.userBalance || 0)}
                                className="confirm-button"
                            >
                                {redeeming ? '...' : ''}
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default TokenList; 